body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* layout css */
.frame {
  height: 95vh;
  padding: 0px;
}

.main {
  height: 95vh;
  padding: 10px;
}

.canvas {
  height: 95vh;
  padding: 10px;
  background-color:	 #ceb3e8
}

.sidecolumn {
  height: 95vh;
  padding: 10px;
  background-color:	#a574d5
}
.inspection {
  height: 95vh;
  padding: 10px;
  background-color: #FFFFFFFF
}

.row {
  background-color:	#a574d5
}

.context-menu { 
  position: absolute;
}

.navbar {
  background-color:	#25587c
}

.nav-link > div {
  color: #f4f4f4f4
}

.navbar-brand > div {
  color: #f4f4f4f4
}

